import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AbstractControl, FormArray, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { CleaveJsService } from 'src/app/cafe/services/cleave-js.service';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-special-time',
  templateUrl: './special-time.component.html',
  styleUrls: ['./special-time.component.scss']
})
export class SpecialTimeComponent {
  /** D liu edit, nu khng c th s l thm mi */
  @Input() data: any;
  /** Mng special_times  check xem c b trng hay khng */
  @Input() specialTimes: any;

  /** Khi ng modal */
  @Output() onClose = new EventEmitter<any>();
  /** Khi submit modal */
  @Output() onOk = new EventEmitter<any>();

  /** FormGroup ca modal */
  specialTimeForm: FormGroup;
  /** Kiu thi gian c bit */
  specialTimeType: 'day' | 'hour' = 'day';
  /** Ngy trong tun */
  daysOfWeekList = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'];

  /** Validator cho trng gi tin */
  PRICE_VALIDATOR = [
    Validators.required,
    Validators.pattern('(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'),
  ];

  management_product_price2: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').management_product_price2;



  constructor(
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private cleaveJsService: CleaveJsService,
  ) { }



  ngOnInit() {
    if(this.data) {
      // Nu c data th s ly kiu thi gian c bit t data
      const fromMinute = this.data?.specific_time?.from_minute
        ? this.data.specific_time.from_minute.getHours() * 60 + this.data.specific_time.from_minute.getMinutes()
        : null;
      const toMinute = this.data?.specific_time?.to_minute
        ? this.data.specific_time.to_minute.getHours() * 60 + this.data.specific_time.to_minute.getMinutes()
        : null;
      this.specialTimeType = fromMinute === 0 && toMinute === 0 ? 'day' : 'hour';
    }
    this.initForm(this.data);
    this.initDaysOfWeek();
  }



  ngAfterViewInit() {
    this.cleaveJsService.createCleaves(['.special_time_price']);
    if(this.management_product_price2) this.cleaveJsService.createCleaves(['.special_time_price2']);
  }



  ngOnDestroy() {
    this.cleaveJsService.destroyCleaves(['.special_time_price']);
    if(this.management_product_price2) this.cleaveJsService.destroyCleaves(['.special_time_price2']);
  }



  /**
   * Khi to form
   * @param data D liu  khi to form nu c th s set theo data, nu khng th s l mc nh nh thm mi
   */
  initForm(data?: any) {
    this.specialTimeForm = new FormGroup({
      price: new FormControl(
        data?.price ?? 0,
        Validators.compose(this.PRICE_VALIDATOR)
      ),
      price2: new FormControl(
        data?.price2 ?? 0,
        Validators.compose(this.PRICE_VALIDATOR)
      ),
      time_slot_name: new FormControl(data?.time_slot_name ?? null, Validators.required),
      specific_time: new FormGroup({
        from_minute: new FormControl(data?.specific_time?.from_minute && this.specialTimeType === 'hour' ? data?.specific_time?.from_minute : null),
        to_minute: new FormControl(data?.specific_time?.to_minute && this.specialTimeType === 'hour' ? data?.specific_time?.to_minute : null),
      }),
      days_of_week: new FormArray([], atLeastOneCheckboxChecked),
    });

    // Nu l Edit v l Khung gi th update validator cho cc trng from_minute v to_minute
    if(this.data && this.specialTimeType === 'hour') {
      this.specialTimeForm.get('specific_time.from_minute').setValidators(Validators.required);
      this.specialTimeForm.get('specific_time.to_minute').setValidators(
        Validators.compose([
          Validators.required,
          timeRangeValidator(),
        ])
      );
      this.specialTimeForm.get('specific_time.from_minute').updateValueAndValidity();
      this.specialTimeForm.get('specific_time.to_minute').updateValueAndValidity();
    }
  }



  /**
   * Khi to thm checkbox cho cc ngy trong tun vo form
   */
  initDaysOfWeek() {
    const daysOfWeekArray = this.specialTimeForm.get('days_of_week') as FormArray;
    this.daysOfWeekList.forEach(day => {
      daysOfWeekArray.push(new FormControl(this.data?.days_of_week?.includes(day) ?? false));
    });
  }
  
  
  
  /** Getters ca days_of_week */
  get daysOfWeekArray(): FormArray {
    return this.specialTimeForm.get('days_of_week') as FormArray;
  }

  /** Ly danh sch cc ngy  chn da trn checkbox vi gi tr true/false */
  getSelectedDays(): string[] {
    return this.daysOfWeekArray.value
      .map((checked: boolean, index: number) => (checked ? this.daysOfWeekList[index] : null))
      .filter(day => day !== null);
  }



  /**
   * Khi thay i gi tr ca loi thi gian l day hay hour (C ngy hay Khung gi) th s set li cc validator cho cc trng
   * @param event Kiu thi gian c bit
   */
  changeTimeType(event: 'day' | 'hour') {
    if(event === 'day') {
      this.specialTimeForm.get('specific_time').patchValue({
        from_minute: null,
        to_minute: null,
      });
      this.specialTimeForm.get('specific_time.from_minute').clearValidators();
      this.specialTimeForm.get('specific_time.to_minute').clearValidators();
    }
    else {
      this.specialTimeForm.get('specific_time.from_minute').setValidators(Validators.required);
      this.specialTimeForm.get('specific_time.to_minute').setValidators(
        Validators.compose([
          Validators.required,
          timeRangeValidator(),
        ])
      );
    }
    this.specialTimeForm.get('specific_time.from_minute').updateValueAndValidity();
    this.specialTimeForm.get('specific_time.to_minute').updateValueAndValidity();
  }



  /**
   * Kim tra xem khong thi gian  chn c b trng vi cc khong thi gian  c hay khng
   * @param selectedDays Mng cc ngy  chn
   * @param from_minute S pht ca from_minute
   * @param to_minute S pht ca to_minute
   * @returns true nu khng b trng, false nu b trng
   */
  checkTimeRangeOverlap(selectedDays, from_minute, to_minute): boolean {
    for (const item of this.specialTimes) {
      if (this.data && this.data.index === this.specialTimes.indexOf(item)) {
        // nu l chnh n th b qua
        continue;
      }
      const existingFromMinute = item.specific_time.from_minute;
      const existingToMinute = item.specific_time.to_minute;
      if (selectedDays.some(day => item.days_of_week.includes(day))) {
        // nu from >= existingFromMinute && from < existingToMinute
        // hoc to > existingFromMinute && to <= existingToMinute
        // th khng hp l
        if ((from_minute >= existingFromMinute && from_minute < existingToMinute) || (to_minute > existingFromMinute && to_minute <= existingToMinute)) {
          return false;
        }
      }
    }
    return true;
  }



  /**
   * Khi nhn nt thm
   */
  onSubmit() {
    // Nu form khng hp l
    if(!this.specialTimeForm.valid) {
      this.specialTimeForm.markAllAsTouched();
      this.specialTimeForm.markAsDirty();
      this.specialTimeForm.updateValueAndValidity();
      this.vhComponent.showToast(
        2000,
        this.languageService.translate('Please enter all valid required information'),
        'alert-toast'
      );
      return;
    }

    // Tnh ton from to v days_of_week
    let fromMinute = 0;
    let toMinute = 1440;
    if (this.specialTimeType === 'hour') {
      const fromTime: Date = this.specialTimeForm.get('specific_time.from_minute').value;
      const toTime: Date = this.specialTimeForm.get('specific_time.to_minute').value;
      fromMinute = fromTime.getHours() * 60 + fromTime.getMinutes();
      toMinute = toTime.getHours() * 60 + toTime.getMinutes();
    }
    const selectedDays = this.getSelectedDays();

    // Kim tra xem c khung gi no b trng hay khng
    const isTimeRangeValid = this.checkTimeRangeOverlap(selectedDays, fromMinute, toMinute);
    if (!isTimeRangeValid) {
      this.vhComponent.showToast(
        2000,
        this.languageService.translate('There is a time period overlapping with an existing time block'),
        'alert-toast'
      );
      return;
    }
    else {
      const data = {
        ...this.specialTimeForm.value,
        price: this.cleaveJsService.getRawValue('.special_time_price'),
        price2: this.management_product_price2 ? this.cleaveJsService.getRawValue('.special_time_price2') : 0,
        specific_time: {
          from_minute: fromMinute,
          to_minute: toMinute,
        },
        days_of_week: selectedDays,
      };

      // Nu c data th s l edit
      if(this.data) {
        data.index = this.data.index;
      }

      this.onOk.emit(data);
    }
  }

}



/**
 * Validator  kim tra xem c t nht mt checkbox c chn hay khng
 * @param control 
 * @returns 
 */
function atLeastOneCheckboxChecked(control: AbstractControl): ValidationErrors | null {
  if (control instanceof FormArray) {
    const isChecked = control.controls.some((checkbox) => checkbox.value === true);
    return isChecked ? null : { required: true }; // Tr v li nu khng c checkbox no c chn
  }
  return null;
}



/**
 * Validator  m bo rng khong thi gian to_minute ln hn from_minute
 */
export function timeRangeValidator(): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (!control.parent) {
      return null; // Nu khng c parent, khng cn kim tra
    }

    const fromTime = control.parent.get('from_minute')?.value;
    const toTime = control.value;

    if (fromTime instanceof Date && toTime instanceof Date) {
      const fromTotalMinutes = fromTime.getHours() * 3600 + fromTime.getMinutes() * 60;
      const toTotalMinutes = toTime.getHours() * 3600 + toTime.getMinutes() * 60;
      if (toTotalMinutes <= fromTotalMinutes) {
        return { timeRangeInvalid: true }; // Validation error
      }
    }
    return null; // Valid
  };
}
