import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { combineLatest } from 'rxjs';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/cafe/interface/vh-type';
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-services-list',
  templateUrl: './services-list.component.html',
  styleUrls: ['./services-list.component.scss']
})
export class ServicesListComponent {
  list_subservice: any = [];
  list_show_service: any = [];
  list_show_service_not: any = [];
  list_service: any = [];
  list_show: any;

  /** Danh sch dch v hin th trong tab  n */
  list_show_service_hidden: any = [];
  /** Danh sch sn phm hin th trong tab  n */
  list_subservice_hidden: any = [];
  list_service_hidden: any = [];
  list_show_hidden: any = [];

  /** Danh sch tt c danh mc */
  list_category: any = [];
  /** Danh sch danh mc tm kim */
  list_search_category: any = [];
  stateObj = {
    /** T kho tm kim */
    keySearch: '',
    /** id ca danh mc c chn */
    selectedValue: '1',
    /** Tab c chn */
    tab: 0
  }
  observableAll: any;
  public VhType = VhType;
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private languageService: LanguageService,
    private cdRef: ChangeDetectorRef,
    private vhComponent:VhComponent
  ) {
    if (this.router.getCurrentNavigation().extras.state) {
      let stateObj = this.router.getCurrentNavigation().extras.state.stateObj;
      this.stateObj.keySearch = stateObj.keySearch;
      this.stateObj.selectedValue = stateObj.selectedValue;
      this.stateObj.tab = stateObj.tab;
    }
  }



  ngOnInit() {
    this.doRefreshData();

  }



  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#service-list") && document.querySelector(".ant-tabs-tab") && document.querySelector(".ant-table-thead") && document.querySelector(".service-list-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#service-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".service-list-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - document.querySelector(".ant-tabs-tab").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }



  ngOnDestroy() {
    this.observableAll.unsubscribe();
  }



  /**get data product, category */
  doRefreshData() {
    this.observableAll = combineLatest([
      this.vhQueryCafe.getObservableLocalProducts(VhType.SERVICE_SERVICETIME),
      this.vhQueryCafe.getObservableLocalCategorys(VhType.SERVICE)
    ]).subscribe((result: any) => {
      console.log(result);
      
      //x l dch v
      this.list_service = result[0].map(item => this.processService(item));
      this.list_subservice = this.list_service;
      if (this.stateObj.keySearch) {
        this.searchProducts(this.stateObj.keySearch);
      }
      else {
        this.searchByCategory(this.stateObj.selectedValue);
      }
      //x l category
      this.list_category = result[1].filter(res => res.type == VhType.SERVICE);
      if (!this.list_category.some(item => item._id == '1')) {
        this.list_category.unshift({ name: this.languageService.translate('All'), _id: '1' });
      }
      this.list_search_category = this.list_category;
    });
  }



  /**
   * X l d liu ca sn phm (name_category)
   * @param item 
   * @returns 
   */
  processService(item: any): any {
    return {
      ...item,
      name_category: this.vhQueryCafe.getlocalCategory(item.id_category) ? this.vhQueryCafe.getlocalCategory(item.id_category).name : '',
      unit: item.units[0].unit,
      barcode: item.units[0].barcode,
    };
  }



  /**
   * Ly danh sch sn phm  n
   */
  getHiddenProducts() {
    this.vhQueryCafe.refreshLocalProducts_Delete_Hidden(VhType.SERVICE_SERVICETIME).then(() => {
      const hiddenProducts = this.vhQueryCafe.getlocalProducts(VhType.SERVICE_SERVICETIME).filter(item => item.delete_hidden);

      this.list_service_hidden = hiddenProducts.map(item => this.processService(item));
      this.list_subservice_hidden = this.vhAlgorithm.sortStringbyASC(this.list_service_hidden, 'name');
      if (this.stateObj.keySearch) {
        this.searchProductsHidden(this.stateObj.keySearch);
      }
      else {
        this.searchByCategoryHidden(this.stateObj.selectedValue);
      }
      
    });
  }

  /**
   * cp nht sp theo trng truyn vo
   * @example this.updateProductByField({name: ''})
   */
  updateProductByField(id, value, parent?, is_sub?) {
    console.log('value', value);
    console.log('id', id);


    this.vhQueryCafe.updateProduct(id, value).then((res) => {
      console.log(res);
      this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Update successfully"), 2000);
      if (!value.delete_hidden) {
        this.list_service_hidden = this.list_service_hidden.filter(item => item._id != id);
        this.list_show_service_hidden = this.list_show_service_hidden.filter(item => item._id != id);
      }
    }, error => {
      console.log(error);
    }).catch(e => {
      console.log(e);
    })


  }


  /** 
   * Lc danh sch sn phm theo allow_sell
   */
  filterByAllowSell() {
    this.list_show_service = this.list_show.filter(item => item.delete_hidden == false);
    // this.list_show_service_not = this.list_show.filter(item => !item.delete_hidden == false);
  }



  /**
   * Tm kim sn phm theo tn
   * @param value gi tr tm kim t input
   */
  searchProducts(value) {
    this.stateObj.keySearch = value;
    this.list_service = this.applySearchFilter(this.list_subservice, this.stateObj.keySearch, ['name']);
    this.searchByCategory(this.stateObj.selectedValue);
  }



  /**
   * Tm kim sn phm (tab  n) theo tn
   * @param value gi tr tm kim t input
   */
  searchProductsHidden(value) {
    this.stateObj.keySearch = value;
    this.list_service_hidden = this.applySearchFilter(this.list_subservice_hidden, this.stateObj.keySearch, ['name']);
    this.searchByCategoryHidden(this.stateObj.selectedValue);
  }



  /**
   * Tm kim sn phm theo tn, nu khng c gi tr tm kim th tr v tt c, ngc li th lc theo tn v barcode
   * @param products 
   * @param searchValue 
   * @param fields 
   * @returns 
   */
  applySearchFilter(products: any[], searchValue: string, fields: string[]): any[] {
    return searchValue
      ? this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(searchValue.toLowerCase()), products, fields)
      : products;
  }



  /** 
   * Lc sn phm theo danh mc sn phm
   */
  searchByCategory(value) {
    this.stateObj.selectedValue = value;
    this.list_show = this.applyCategoryFilter(this.list_service, value);
    this.filterByAllowSell();
  }



  /** 
   * Lc sn phm (tab  n) theo danh mc sn phm
   */
  searchByCategoryHidden(value) {
    this.stateObj.selectedValue = value;
    this.list_show_hidden = this.applyCategoryFilter(this.list_service_hidden, value);
    this.list_show_service_hidden = this.list_show_hidden;
  }



  /**
   * Lc danh sch sn phm theo danh mc, nu danh mc l '1' th tr v tt c sn phm, nu khng th lc theo id_category truyn vo
   * @param products 
   * @param categoryId 
   * @returns 
   */
  applyCategoryFilter(products: any[], categoryId: string): any[] {
    return categoryId === '1'
      ? products
      : products.filter(item => item.id_category === categoryId);
  }



  /** 
   * Lc sn phm theo danh mc sn phm
   */
  search(value) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.list_category = this.vhAlgorithm.searchList(tempVal, this.list_search_category, ['name']);
    else this.list_category = this.list_search_category;
  }



  /**
   * Khi thay i tab
   */
  changeTab(value) {
    this.stateObj.tab = value;
    if (value == '1') {
      this.getHiddenProducts();
    }
  }



  goBack() {
    this.router.navigate(['/cafe/dashboard/management/service']);
  }


  gotoAddService() {
    this.router.navigate(['/cafe/dashboard/management/service/service-list/add'], { state: { stateObj: this.stateObj } });
  }


  gotoCreateDataService() {
    let dataRestore = { keySearch: this.stateObj.keySearch, selectedValue: this.stateObj.selectedValue, selectedIndex: this.stateObj.tab }
    this.router.navigate(['/cafe/dashboard/management/service/service-list/create-data-service'], { state: { dataRestore } });
  }


  gotoDetailService(data, isFromHidden?: boolean) {
    const state = {
      data: data,
      stateObj: this.stateObj
    }
    if (isFromHidden) {
      state['isFromHidden'] = true;
    }
    console.log(state);
    this.router.navigate(['/cafe/dashboard/management/service/service-list/detail'], {
      state: state
    });
  }



  /** hm sort cho cc ct */
  name_col = false;          // chn sort ct tn sn phm
  unit_col = false;             // Chn sp xp theo n v
  barcode_col = false;          // Chn sp xp theo barcode
  name_category_col = false;     // Chn sp xp theo tn danh mc
  price_col = false;            // Chn sp xp theo gi
  type_col = false;            // Chn sp xp theo gi

  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   * @param typeList      // Bin dng  xc nh danh sch sp xp l cho bn hay khng cho bn
   */
  sortTable(colName, typeList: 'not' | 'allow' | 'hidden') {
    let methodName = (colName === 'price' || colName === 'type') ? 'sortNumberby' : 'sortVietnameseby';
    let direction = this[colName + '_col'] ? 'ASC' : 'DESC';
    let listToSort = typeList === 'not'
      ? 'list_show_service_not'
      : typeList === 'allow'
        ? 'list_show_service'
        : 'list_show_service_hidden';

    this[listToSort] = this.vhAlgorithm[methodName + direction]([...this[listToSort]], colName);
  }



  /**
   * Xut tt c service
   * xut ra file excel v ti v
   */
  exportListService() {
    let serviceExcel: Array<any> = [];
    this.list_service.forEach((product, index) => {
      let row: Object = new Object();
      const category: any = this.vhQueryCafe.getlocalCategory(product['id_category']);
      row[this.languageService.translate("_name_service")] = product['name'];
      row[this.languageService.translate("_category")] = category ? category['name'] : '';
      row[this.languageService.translate("_unit")] = product['unit'];
      row[this.languageService.translate("_price")] = product['units'][0]['price'];
      row[this.languageService.translate("_barcode")] = product['barcode'];
      serviceExcel.push(row);
      if (index == this.list_service.length - 1) {
        this.vhAlgorithm.exportXLSX(serviceExcel, this.languageService.translate("Service list"));
      }
    });
  }



  /**
   * Xut service Theo cng
   * xut ra file excel v ti v
   */
  exportListServiceByWork() {
    let serviceExcel: Array<any> = [];
    const serviceToExport = this.list_service.filter(s => s.type === VhType.SERVICE);
    serviceToExport.forEach((product, index) => {
      let row: Object = new Object();
      const category: any = this.vhQueryCafe.getlocalCategory(product['id_category']);
      row[this.languageService.translate("_name_service")] = product['name'];
      row[this.languageService.translate("_category")] = category ? category['name'] : '';
      row[this.languageService.translate("_unit")] = product['unit'];
      row[this.languageService.translate("_price")] = product['units'][0]['price'];
      row[this.languageService.translate("_barcode")] = product['barcode'];
      serviceExcel.push(row);
      if (index == serviceToExport.length - 1) {
        this.vhAlgorithm.exportXLSX(serviceExcel, this.languageService.translate("Service list"));
      }
    });
  }



  /**
   * Xut service Theo thi gian
   * xut ra file excel v ti v
   */
  exportListServiceByTime() {
    let serviceExcel: Array<any> = [];
    const serviceToExport = this.list_service.filter(s => s.type === VhType.SERVICETIME);
  
    serviceToExport.forEach((product, index) => {
      let row: any = {};
  
      // Tn dch v
      row[this.languageService.translate('_name_service')] = product['name'];
      // Danh mc
      const category: any = this.vhQueryCafe.getlocalCategory(product['id_category']);
      row[this.languageService.translate('_category')] = category ? category['name'] : '';
      // Barcode
      row[this.languageService.translate('_barcode')] = product['barcode'];
      // Gi bn (Gi bn thng)
      row[this.languageService.translate('_price_regular')] = product['units'][0]?.['price'];
      // Khong thi gian (Gi bn thng)
      row[this.languageService.translate('_time_block_regular')] = product['units'][0]?.['time_block'];
      // n v (Gi bn thng)
      row[this.languageService.translate('_unit_regular')] = this.languageService.translate(product['units'][0]?.['unit']);
      // Gi bn (Gp block)
      row[this.languageService.translate('_price_merge_block')] = product['units'][0]?.['merge_block']?.price;
      // S lng block gp (Gp block)
      row[this.languageService.translate('_merge_block_quantity')] = product['units'][0]?.['merge_block']?.number;
      // Gi bn (Gi u tin)
      row[this.languageService.translate('_price_first_time')] = product['units'][0]?.['first_time']?.price;
      // Khong thi gian (Gi u tin)
      row[this.languageService.translate('_time_block_first_time')] = product['units'][0]?.['first_time']?.time_block;
      // n v (Gi u tin)
      row[this.languageService.translate('_unit_first_time')] = this.languageService.translate(product['units'][0]?.['first_time']?.unit);
      // Gi c bit
      if (product['units'][0]?.['special_times'] && product['units'][0]?.['special_times'].length > 0) {
        row[this.languageService.translate('_special_times')] = product['units'][0]?.['special_times']
          .map((special: any) => {
            const translatedDays = special.days_of_week
              .map((day: string) => this.languageService.translate(day))
              .join('-');
      
            return `${special.time_slot_name}, ${special.price}, ${special.specific_time.from_minute}-${special.specific_time.to_minute}, ${translatedDays}`;
          })
          .join('; ');
      }   
      else {
        row[this.languageService.translate('_special_times')] = '';
      }
  
      serviceExcel.push(row);
  
      // Export file nu l dng cui (sau khi thm tt c cc dng)
      if (index === serviceToExport.length - 1) {
        this.vhAlgorithm.exportXLSX(serviceExcel, this.languageService.translate('Service list'));
      }
    });
  }

}
