import { Component } from '@angular/core';
import { FormGroup, FormControl, Validators, AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { VhQueryCafe, VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhType } from 'src/app/cafe/interface/vh-type';
import { LanguageService } from 'src/app/cafe/services/language.service';
declare var electron: any;

@Component({
  selector: 'app-create-data-service',
  templateUrl: './create-data-service.component.html',
  styleUrls: ['./create-data-service.component.scss']
})
export class CreateDataServiceComponent {

  formData: FormGroup[] = [];
  localProduct: any[] = this.vhQueryCafe.getlocalProducts().filter(item => item.type == VhType.SERVICE);
  localCategory: any[] = this.vhQueryCafe.getlocalCategorys().filter(item => item.type == VhType.SERVICE);
  nameFile: string = '';
  url: any = '/assets/documents/list-service/' + this.languageService.translate('Service list') + '.xlsx';
  urlByTime: any = '/assets/documents/list-service/' + this.languageService.translate('Service list by time') + '.xlsx';
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore;
  /** Validator cho trng gi tin */
  PRICE_VALIDATOR = [
    Validators.required,
    Validators.pattern('(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'),
  ];
  /** File excel import vo c phi l d liu ca Dch v thi gian hay khng */
  file_import_is_servicetime: boolean = false;
  /** Danh sch loi khong thi gian */
  time_unit_list: any = [
    { _id: 'day', name: 'Day' },
    { _id: 'hour', name: 'Hour' },
    { _id: 'minute', name: 'Minute' },
    // { _id: 'second', name: 'Second' },
  ];



  constructor(
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private router: Router,
    private languageService: LanguageService,
    private nzModalService: NzModalService,
    private platform: Platform,
    private vhQuery: VhQuery
  ) { }
  


  validation_messages = {
    name: [{ type: "required", message: this.languageService.translate('Name is required') }],
    status_barcode: [{ message: this.languageService.translate('Invalid barcode') }],
    category: [{ type: 'required', message: this.languageService.translate('Category is required') }],
    status_name: [{ message: this.languageService.translate('Invalid name') }],
    price: [{ type: 'required', message: this.languageService.translate('Price is required') }, { type: 'pattern', message: this.languageService.translate('Invalid price') }],
    
    time_block: [{ type: 'required', message: this.languageService.translate('Time block is required') }, { type: 'min', message: this.languageService.translate('Time block must be greater than 0') }],
    merge_block_price: [{ type: 'required', message: this.languageService.translate('Price is required') }, { type: 'pattern', message: this.languageService.translate('Invalid price') }],
    merge_block_number: [{ type: 'required', message: this.languageService.translate('Quantity is required') }, { type: 'min', message: this.languageService.translate('Quantity must be greater than 0') }],
    first_time_price: [{ type: 'required', message: this.languageService.translate('Price is required') }, { type: 'pattern', message: this.languageService.translate('Invalid price') }],
    first_time_time_block: [{ type: 'required', message: this.languageService.translate('Time block is required') }, { type: 'min', message: this.languageService.translate('Time block must be greater than 0') }],
    special_times: [{ type: 'required', message: this.languageService.translate('Special times is required') }, { type: 'invalidFormat', message: this.languageService.translate('Invalid special times format') }],
  };



  initForm(data, index) {
    const form = new FormGroup({
      id: new FormControl(index),
      name: new FormControl(data[this.languageService.translate('_name_service')], [Validators.required]),
      status_name: new FormControl(false), // trng thi barcode true: trng / false: oke
      category: new FormControl(data[this.languageService.translate('_category')], [Validators.required]),
      unit: new FormControl(this.getUnitMapping(data[this.languageService.translate('_unit')] ?? data[this.languageService.translate('_unit_regular')]), [Validators.required]),
      price: new FormControl(data[this.languageService.translate('_price')] ?? data[this.languageService.translate('_price_regular')] ?? 0, this.PRICE_VALIDATOR),
      barcode: new FormControl(data[this.languageService.translate('_barcode')]),
      status_barcode: new FormControl(false), // trng thi barcode true: trng / false: oke
      
      time_block: new FormControl(data[this.languageService.translate('_time_block_regular')] ?? 1, [Validators.required, Validators.min(1)]),

      is_merge_block: new FormControl(data[this.languageService.translate('_merge_block_quantity')] !== undefined, [Validators.required]),
      is_first_time: new FormControl(data[this.languageService.translate('_time_block_first_time')] !== undefined, [Validators.required]),
      is_special_times: new FormControl(data[this.languageService.translate('_special_times')]?.length > 0, [Validators.required]),
    });

    // merge_block
    form.addControl('merge_block', 
      new FormGroup({
        price: new FormControl(data[this.languageService.translate('_price_merge_block')] ?? 0, this.PRICE_VALIDATOR),
        number: new FormControl(data[this.languageService.translate('_merge_block_quantity')] ?? 1, [Validators.required, Validators.min(1)]),
      })
    )

    // first_time
    form.addControl('first_time', 
      new FormGroup({
        price: new FormControl(data[this.languageService.translate('_price_first_time')] ?? 0, this.PRICE_VALIDATOR),
        time_block: new FormControl(data[this.languageService.translate('_time_block_first_time')] ?? 1, [Validators.required, Validators.min(1)]),
        unit: new FormControl(this.getUnitMapping(data[this.languageService.translate('_unit_first_time')] ?? data[this.languageService.translate('_unit_regular')]), [Validators.required]),
      })
    )

    // special_times
    form.addControl('special_times', new FormControl(data[this.languageService.translate("_special_times")] ?? '', [this.specialTimesValidator()]));

    return form;
  }



  checkModelChange() {
    this.formData.forEach((form, index) => {
      let status: any = this.checkNameAndBarcode(form['value']['name'], form['value']['barcode'], form['value']['id']);
      form.controls['status_name'].setValue(status[0] ? true : status[2]);
      form.controls['status_barcode'].setValue(status[1] ? true : status[3]);
    });
  }



  /**Check name v barcode v ly id_product*/
  checkNameAndBarcode(name, barcode, id_form?: any) {
    let checkDBName: any = name != null ? this.localProduct.find(item => item.name.trim() == name.toString().trim()) : null;
    let checkDBBarcode: any = barcode != null ? this.localProduct.find(item => item.barcode == barcode.toString().trim()) : null;
    //-----CHECK DC-----//
    let checkName: any = name != null ? ((this.formData.filter(item => item['value'].name.trim() == name.trim() && item['value']['id'] != id_form).length == 0) ? false : true) : null;
    let checkBarcode: any = barcode != null ? ((this.formData.filter(item => item['value'].barcode == barcode && item['value']['id'] != id_form).length == 0 || barcode.length == 0) ? false : true) : null;
    return [checkDBName ? checkDBName['_id'] : null, checkDBBarcode ? checkDBBarcode['_id'] : null, checkName, checkBarcode];
  }



  /**Chn file */
  openFile() {
    document.getElementById("file_excel_product")['value'] = '';
    document.getElementById("file_excel_product").click();
  }



  /**
   * M File (Nhp tp)
   */
  openXLSXFile(events) {
    if (events.target.files[0]) {
      this.file_import_is_servicetime = false; // mc nh l false

      // lu tn file
      this.nameFile = events.target.files[0]['name'].slice(0, events.target.files[0]['name'].lastIndexOf("."));

      this.vhAlgorithm.importXLSX(events.target.files[0]).then((data: any) => {
        console.log(data)
        // Nu qu 200
        if (data.length > 200) {
          this.vhComponent.alertMessage("", this.languageService.translate("Only 200 products can be imported at a time"), "", "OK").then(() => {
            this.formData = [];
          });
        } 
        // <= 200
        else {
          this.formData = [];
          if (data.length) {
            this.vhComponent.showLoading("").then(() => {
              // Kim tra c phi l dch v theo thi gian hay khng, da trn trng "time_block" ca data[0]
              if(data[0][this.languageService.translate('_time_block_regular')] !== undefined) {
                this.file_import_is_servicetime = true;
              }
              data.forEach((item, index) => {
                this.formData.push(this.initForm(item, index));
                // Nu l dng cui
                if (index == data.length - 1) {
                  this.vhComponent.hideLoading(0);
                  this.checkModelChange();
                }
              });
            });
          }
        }
      }).catch(err => {
        this.vhComponent.hideLoading(0);
        this.formData = [];
        this.vhComponent.alertMessage(this.languageService.translate("Error"), this.languageService.translate("Fail file format"), this.languageService.translate("Cancel"));
      });
    }
  }



  /** Xut ra file CSV hoc ti v file mu */
  status: boolean = false;
  exportXLSXFile() {
    if (this.formData.length) {
      let XLSXData: any[] = [];
      if(!this.file_import_is_servicetime) {
        this.formData.forEach((item, index) => {
          let row: Object = new Object;
          row[this.languageService.translate("_name_service")] = item['value']['name'];
          row[this.languageService.translate("_category")] = item['value']['category'];
          row[this.languageService.translate("_unit")] = item['value']['unit'];
          row[this.languageService.translate("_price")] = item['value']['price'];
          row[this.languageService.translate("_barcode")] = item['value']['barcode'];
          XLSXData.push(row);
          if (index == this.formData.length - 1) {
            this.vhAlgorithm.exportXLSX(XLSXData, this.nameFile).then(() => this.status = true).catch(err => this.status = true);
          }
        });
      }
      else {
        this.formData.forEach((item, index) => {
          let row: any = {};
          // Tn dch v
          row[this.languageService.translate('_name_service')] = item['value']['name'];
          // Danh mc
          row[this.languageService.translate('_category')] = item['value']['category'];
          // Barcode
          row[this.languageService.translate('_barcode')] = item['value']['barcode'];
          // Gi bn (Gi bn thng)
          row[this.languageService.translate('_price_regular')] = item['value']?.['price'];
          // Khong thi gian (Gi bn thng)
          row[this.languageService.translate('_time_block_regular')] = item['value']?.['time_block'];
          // n v (Gi bn thng)
          row[this.languageService.translate('_unit_regular')] = this.languageService.translate(item['value']?.['unit']);

          if(item['value']?.['is_merge_block']) {
            // Gi bn (Gp block)
            row[this.languageService.translate('_price_merge_block')] = item['value']?.['merge_block']?.price;
            // S lng block gp (Gp block)
            row[this.languageService.translate('_merge_block_quantity')] = item['value']?.['merge_block']?.number;
          }

          if(item['value']?.['is_first_time']) {
            // Gi bn (Gi u tin)
            row[this.languageService.translate('_price_first_time')] = item['value']?.['first_time']?.price;
            // Khong thi gian (Gi u tin)
            row[this.languageService.translate('_time_block_first_time')] = item['value']?.['first_time']?.time_block;
            // n v (Gi u tin)
            row[this.languageService.translate('_unit_first_time')] = this.languageService.translate(item['value']?.['first_time']?.unit);
          }

          if(item['value']?.['is_special_times']) {
            // Gi c bit
            row[this.languageService.translate('_special_times')] = item['value']?.['special_times'];
          }

          XLSXData.push(row);
      
          // Export file nu l dng cui (sau khi thm tt c cc dng)
          if (index === this.formData.length - 1) {
            this.vhAlgorithm.exportXLSX(XLSXData, this.nameFile).then(() => this.status = true).catch(err => this.status = true);
          }
        });
      }
    }
  }



  /** 
   * Ti file mu Dch v theo cng
   */
  downloadSampleFileServiceByWork() {
    if (this.platform.is("electron")) {
      let urlEn = "https://github.com/Viethas-App/Download-Sample-File/releases/download/Download-Sample-File/Service-list.xlsx";
      let urlVI = "https://github.com/Viethas-App/Download-Sample-File/releases/download/Download-Sample-File/Danh-sach-dich-vu.xlsx";
      let appLang: any = localStorage.getItem("vh-cafe-language");
      if(appLang == 'vi') window.open(urlVI,'_blank', 'location=yes,height=250,width=250,scrollbars=yes,status=yes,title=Download');
      else window.open(urlEn,'_blank', 'location=yes,height=250,width=250,scrollbars=yes,status=yes,title=Download');
    }
    else {
      document.getElementById("download_product").click();
    }
  }



  /** 
   * Ti file mu Dch v theo thi gian
   */
  downloadSampleFileServiceByTime() {
    if (this.platform.is("electron")){
      let urlEn = "https://github.com/Viethas-App/Sales365-Desktop-Electron/raw/main/assets/data/Service-list-by-time.xlsx";
      let urlVI = "https://github.com/Viethas-App/Sales365-Desktop-Electron/raw/main/assets/data/Danh-sach-dich-vu-theo-thoi-gian.xlsx";
      let appLang: any = localStorage.getItem("vh-cafe-language");
      if(appLang == 'vi') window.open(urlVI,'_blank', 'location=yes,height=250,width=250,scrollbars=yes,status=yes,title=Download');
      else window.open(urlEn,'_blank', 'location=yes,height=250,width=250,scrollbars=yes,status=yes,title=Download');
    }
    else {
      document.getElementById("download_product_by_time").click();
    }
  }



  /**Check d liu trc khi to d liu */
  checkForm(): boolean {
    for (let i = 0; i < this.formData.length; i++) {
      if (
        this.formData[i]['value'].status_barcode == true 
        || this.formData[i]['value'].status_name == true
        || this.formData[i]['controls']['name'].invalid
        || this.formData[i]['controls']['category'].invalid
        || this.formData[i]['controls']['unit'].invalid
        || this.formData[i]['controls']['price'].invalid
        || (
          this.formData[i].invalid && (
            (this.formData[i]['value'].is_first_time && this.formData[i].controls['first_time'].invalid)
            || (this.formData[i]['value'].is_merge_block && this.formData[i].controls['merge_block'].invalid)
            || (this.formData[i]['value'].is_special_times && this.formData[i].controls['special_times'].invalid)
          )
        )
      ) {
        this.vhComponent.alertMessage(this.languageService.translate("Error"), `${this.languageService.translate("Error line")} ${i + 1}`, this.languageService.translate("Cancel"))
        return false;
      }
      if (i == this.formData.length - 1) {
        return true;
      }
    }
  }



  createdError: string = "";
  createData() {
    if (this.checkForm()) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", this.languageService.translate("Create service data"), "OK", this.languageService.translate("Cancel"))
        .then(res => {
          this.createCategoriesAndProduct();
        }, () => { });
    }
  }



  /**To d liu danh mc v sn phm */
  createCategoriesAndProduct() {
    this.vhComponent.showLoading("", "current-loading").then(async () => {
      // ly d liu tn category
      const dataCategory: any[] = [...new Set(this.formData.map(item => item['value']['category']))]
      // tm ra d liu category cha c to database
      let createdCategory: any[] = [] // danh sch danh mc c to
      dataCategory.forEach((item, index) => {
        if (item && !this.localCategory.some(cate => this.vhAlgorithm.changeAlias(cate.name.trim()).toLowerCase() == this.vhAlgorithm.changeAlias(item.trim()).toLowerCase()))
          createdCategory.push({ name: item, type: 2 })
      })

      try {
        const categories: any = await this.vhQueryCafe.addCategorys(createdCategory);

        if (categories) {
          this.localCategory = [...this.localCategory.concat([...categories])]
        }
        else {
          throw Error("Categories have been created failed");
        }

        // to sn phm
        const dataProduct: any[] = this.formData.map(form => {
          const category: any = this.localCategory.find(cate => this.vhAlgorithm.changeAlias(cate['name'].trim()).toLowerCase() == this.vhAlgorithm.changeAlias(form['value']['category'].trim()).toLowerCase())
          
          let service: any = {
            name: form['value']['name'].trim(),
            time_warning : 0,
            img: '',
            id_category: category?._id,
            tax: 0,
            // allow_sell: true,
            delete_hidden: false,
            type: this.file_import_is_servicetime ? VhType.SERVICETIME : VhType.SERVICE,
            units : [{
              unit: form['value']['unit'] ? form['value']['unit'].trim() : '',
              price: parseFloat(form['value']['price']),
              price2 : 0,
              barcode: form['value']['barcode'] ? form['value']['barcode'] : '',
              ratio : 1,
              default : true,
            }],
          }

          if (this.file_import_is_servicetime) {
            // set li units[0] dng time
            service.units[0].price = parseFloat(form['value']['price']);
            service.units[0].price2 = 0;
            service.units[0].time_block = form['value']['time_block'];
      
            // cc trng is_merge_block, is_first_time, is_special_times
            service.units[0].is_merge_block = form['value']['is_merge_block'];
            service.units[0].is_first_time = form['value']['is_first_time'];
            service.units[0].is_special_times = form['value']['is_special_times'];
      
            // merge_block
            if(service.units[0].is_merge_block) {
              service.units[0].merge_block = {
                price: parseFloat(form['value']['merge_block']['price']),
                price2: 0,
                number: form['value']['merge_block']['number'],
              };
            }
            // first_time
            if (service.units[0].is_first_time) {
              service.units[0].first_time = {
                price: parseFloat(form['value']['first_time']['price']),
                price2: 0,
                unit: form['value']['first_time']['unit'],
                time_block: form['value']['first_time']['time_block'],
              };
            }
            // special_times
            if (service.units[0].is_special_times) {
              service.units[0].special_times = [];
              let special_times = form['value']['special_times'].split(';').filter((entry) => entry.trim() !== '');
              for (let i = 0; i < special_times.length; i++) {
                let special_time = special_times[i].split(',').map((part) => part.trim());
                if (special_time.length == 4) {
                  const [time_slot_name, price, specific_time, days_of_week] = special_time;
                  service.units[0].special_times.push({
                    time_slot_name: time_slot_name,
                    price: parseFloat(price),
                    price2: 0,
                    specific_time: specific_time,
                    days_of_week: days_of_week.split('-').map((day) => this.daysOfWeekMapping[this.vhAlgorithm.changeAlias(day.toLocaleLowerCase())]),
                  });
                }
              }
            }
          }

          return service;
        })

        // Mi ln add 5 ci, xong mi add tip 5 ci k
        const batchSize = 5;
        let addedProducts = [];
        for (let i = 0; i < dataProduct.length; i += batchSize) {
          // ly 5 ci
          const batch = dataProduct.slice(i, i + batchSize);
          try {
            const promises = batch.map(async (product) => {
              return await this.vhQueryCafe.addProducts([product]);
            });
            // add
            const results = await this.vhQuery.asyncPromiseAll(promises);
            // sau khi add xong
            console.log(`Batch ${Math.floor(i / batchSize) + 1} added successfully`);
            results.forEach((products, index) => {
              if (products) {
                addedProducts.push(...products); // Thm cc sn phm  thm vo mng addedProducts
              } else {
                throw Error("Services have been created failed");
              }
            });
          } catch (error) {
            console.error(`Error in batch ${Math.floor(i / batchSize) + 1}:`, error);
          }
        }
        // add xong
        this.vhComponent.hideLoading(0);
        this.showLog([...dataProduct], [...addedProducts])

      } catch (error) {
        console.error(error);
        this.vhComponent.showToast(1500, `${this.languageService.translate("An error occurred while adding product. Please try again")}`, "success-toast");
      }

      this.vhComponent.hideLoading(0);
    })
  }



  showLog(data: any[], dataSuccess: any[]) {
    this.createdError = "";
    if (data.length != dataSuccess.length) {
      for (let product of data) {
        if (!dataSuccess.some(item => item.phone == product.phone))
          this.createdError += `<span> - ${product.name}(${product.phone})</span><br/>`;
      }

      this.nzModalService.error({
        nzTitle: this.languageService.translate("Services have been created failed"),
        nzContent: this.createdError
      });

      this.formData = [];

    } else {
      this.vhComponent.showToast(1500, `${this.languageService.translate("Services")} ${this.languageService.translate("has been added successfully")}`, "success-toast");
      this.goBack();
    }
  }



  goBack() {
    this.router.navigate(['/cafe/dashboard/management/service/service-list'], { state: { stateObj: this.dataRestore } });
  }



  /**
   * Chuyn i tn n v t ting Vit / ting Anh sang nh dng  lu vo database
   */
  daysOfWeekMapping: { [key: string]: string } = {
    'thu 2': 'monday',
    'thu 3': 'tuesday',
    'thu 4': 'wednesday',
    'thu 5': 'thursday',
    'thu 6': 'friday',
    'thu 7': 'saturday',
    'chu nhat': 'sunday',
    'monday': 'monday',
    'tuesday': 'tuesday',
    'wednesday': 'wednesday',
    'thursday': 'thursday',
    'friday': 'friday',
    'saturday': 'saturday',
    'sunday': 'sunday',
  };

  /** Chuyn i tn n v t ting Vit / ting Anh sang nh dng  lu vo database */
  unitMapping: { [key: string]: string } = {
    'ngay': 'day',
    'gio': 'hour',
    'phut': 'minute',
    'giay': 'second',
    'day': 'day',
    'hour': 'hour',
    'minute': 'minute',
    'second': 'second',
  };



  /**
   * Ly tn n v t ting Vit / ting Anh sang dnh dng lu v database.
   * 
   * S gi changeAlias() v toLocaleLowerCase()  chuyn i tn n v sang nh dng chun.
   * 
   * Gi tr mc nh l 'hour' nu khng tm thy tn n v trong danh sch chuyn i.
   * 
   * @returns {string} Tn n v  chuyn i. Nu khng tm thy tn n v trong danh sch chuyn i, tr v 'hour'.
   * @example
   * getUnitMapping('ngy') // tr v 'day'
   * getUnitMapping('   Hour ') // tr v 'hour'
   */
  getUnitMapping(value) {
    return !this.file_import_is_servicetime 
              ? value 
              : (this.unitMapping[this.vhAlgorithm.changeAlias(value ?? 'hour')?.toLocaleLowerCase()]);
  }



  /**
   * Validate special times format theo nh dng: time_slot_name, price, specific_time.from_minute-specific_time.to_minute, days_of_week.
   * 
   * Ngy thng, 80000, 0-1440, th 2-th 3-th 4-th 5;
   * 
   * - Phi c 4 phn t cch nhau bi du phy (,)
   * - Tn khng c  trng
   * - Gi khng c  trng
   * - Thi gian phi nm trong khong t 0 n 1440 pht
   * - Thi gian bt u phi nh hn thi gian kt thc
   * - Ngy phi c phn cch bi du gch ngang (-)
   * - Ngy khng c  trng
   * - Ngy phi nm trong danh sch cc ngy trong tun (monday, tuesday, wednesday, thursday, friday, saturday, sunday)
   * 
   * @returns {ValidatorFn} Hm validator
   * @example
   * day_of_week: new FormControl('', [this.specialTimesValidator()])
   */
  specialTimesValidator(): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const value = control.value;

      //  trng
      if (!value || typeof value !== 'string') {
        return { invalidFormat: 'Value is required' };
      }

      const specialTimeRanges: any[] = []; // Cha danh sch object special time  check overlap
      const entries = value.split(';').filter((entry) => entry.trim() !== '');
      for (const entry of entries) {
        // 4 phn t cch nhau bi du ,
        const parts = entry.split(',').map((part) => part.trim());
        if (parts.length !== 4) {
          return { invalidFormat: 'Each entry must have 4 parts separated by commas. Example: Normal day, 80000, 0-1440, monday-tuesday-wednesday;' };
        }

        const [time_slot_name, price, specific_time, days_of_week] = parts;

        // Tn  trng
        if (!time_slot_name) {
          return { invalidFormat: 'Name is required' };
        }

        // Gi  trng
        if (isNaN(parseFloat(price)) || parseFloat(price) <= 0) {
          return { invalidFormat: 'Price is required' };
        }

        // Thi gian phi nm trong khong t 0 n 1440 pht, Thi gian bt u phi nh hn thi gian kt thc
        const timeParts = specific_time.split('-').map((time) => parseInt(time.trim(), 10));
        if (
          timeParts.length !== 2 ||
          isNaN(timeParts[0]) ||
          isNaN(timeParts[1]) ||
          timeParts[0] < 0 ||
          timeParts[1] > 1440 ||
          timeParts[0] >= timeParts[1]
        ) {
          return { invalidFormat: 'Invalid time range. Must be between 0 and 1440 minutes, and from_minute must be less than to_minute.' };
        }

        // Phi c t nht 1 ngy c chn
        const days = days_of_week.split('-').map((day) => day.trim());
        if (days.length === 0) {
          return { invalidFormat: 'At least one day must be selected' };
        }
        
        // Ngy phi nm trong danh sch cc ngy trong tun
        if(!days.every((day) => this.daysOfWeekMapping[this.vhAlgorithm.changeAlias(day.toLocaleLowerCase())])) {
          return { invalidFormat: 'Invalid days of week. Must be one or more of: Monday, Tuesday, Wednesday, Thursday, Friday, Saturday, Sunday.' };
        }

        // Add v  lt na kim tra overlap
        specialTimeRanges.push({
          days_of_week: days,
          from_minute: timeParts[0],
          to_minute: timeParts[1],
        });
      }

      // Check overlapp thi gian
      for (let i = 0; i < specialTimeRanges.length; i++) {
        const currentEntry = specialTimeRanges[i];
        for (let j = 0; j < specialTimeRanges.length; j++) {
          if (i === j) continue; // B qua nu l chnh n
          const otherEntry = specialTimeRanges[j];
          if (
            currentEntry.days_of_week.some((day) => otherEntry.days_of_week.includes(day)) && // Overlapping ngy
            ((currentEntry.from_minute >= otherEntry.from_minute && currentEntry.from_minute < otherEntry.to_minute) || // Overlapping thI gian bt u
              (currentEntry.to_minute > otherEntry.from_minute && currentEntry.to_minute <= otherEntry.to_minute)) // Overlapping thi gian kt thc
          ) {
            return { invalidFormat: 'There is a time period overlapping with an existing time block' };
          }
        }
      }

      return null; // Valid
    };
  }

}