import { Component, ViewChild } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhImage, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { CleaveJsService } from 'src/app/cafe/services/cleave-js.service';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-add-service',
  templateUrl: './add-service.component.html',
  styleUrls: ['./add-service.component.scss'],
})
export class AddServiceComponent {

  constructor(
    private router: Router,
    private vhImage: VhImage,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private cleaveJsService: CleaveJsService,
  ) {}



  productServiceForm: FormGroup;
  barcode: boolean = false;
  list_category: Array<any> = [];
  list_barcode: Array<any> = [];
  price: any;
  price2: any;
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore;
  management_product_price2: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').management_product_price2;
  createProd: boolean = false;

  /** Danh sch loi dch v */
  item_type_list: any = [
    { _id: VhType.SERVICE, name: 'Charge by work' },
    { _id: VhType.SERVICETIME, name: 'Charge by time' },
  ];

  /** Danh sch loi khong thi gian */
  time_unit_list: any = [
    { _id: 'day', name: 'Day' },
    { _id: 'hour', name: 'Hour' },
    { _id: 'minute', name: 'Minute' },
    // { _id: 'second', name: 'Second' },
  ];

  /** C s dng Gi u tin hay khng */
  use_first_time: boolean = false;
  /** C s dng Gi c bit hay khng */
  use_special_time: boolean = false;
  /** C hin modal chn thi gian c bit hay khng */
  isShowModalSpecialTime: boolean = false;
  /** K hiu tin t. V d 'VND' */
  currencySymbol = JSON.parse(localStorage.getItem('vhsales_currencyFormat')).symbol.text;
  /** D liu thi gian c bit  edit truyn vo modal */
  dataSpecialTimeEdit: any = null;

  /** Validator cho trng gi tin */
  PRICE_VALIDATOR = [
    Validators.required,
    Validators.pattern('(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)'),
  ];

  public VhType = VhType;



  ngOnInit() {
    this.initForm();
    this.initData();
  }



  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.price = this.vhAlgorithm.vhnumeral('.price');
      if (this.management_product_price2) {
        this.price2 = this.vhAlgorithm.vhnumeral('.price2');
      }
    });
  }



  ngOnDestroy() {
    if (!this.createProd && this.productServiceForm.value['img'])
      this.vhImage
        .deleteThumbnail([this.productServiceForm.value['img']])
        .then(() => {});
  }



  goBack() {
    this.router.navigate(['/cafe/dashboard/management/service/service-list'], {
      state: this.dataRestore,
    });
  }



  /**
   * khi to cc trng ca dch v vo form
   * @example this.initForm()
   */
  initForm() {
    this.productServiceForm = new FormGroup({
      img: new FormControl(''),
      name: new FormControl('', Validators.required),
      barcode: new FormControl(''),
      unit: new FormControl('', Validators.required),
      price: new FormControl(0, Validators.compose(this.PRICE_VALIDATOR)),
      price2: new FormControl(0, Validators.compose(this.PRICE_VALIDATOR)),
      id_category: new FormControl('', Validators.required),
      // allow_sell: new FormControl(true),
      delete_hidden: new FormControl(false),
      selling: new FormControl(true),
      type: new FormControl(VhType.SERVICE),
      time_warning: new FormControl(0),

      time_block: new FormControl(1, Validators.compose([Validators.required, Validators.min(1)])),

      is_merge_block: new FormControl(false, Validators.required),
      is_first_time: new FormControl(false, Validators.required),
      is_special_times: new FormControl(false, Validators.required),

      merge_block: new FormGroup({
        price: new FormControl(0, Validators.compose(this.PRICE_VALIDATOR)),
        price2: new FormControl(0, Validators.compose(this.PRICE_VALIDATOR)),
        number: new FormControl(1, Validators.compose([Validators.required, Validators.min(1)])),
      }),

      first_time: new FormGroup({
        price: new FormControl(0, Validators.compose(this.PRICE_VALIDATOR)),
        price2: new FormControl(0, Validators.compose(this.PRICE_VALIDATOR)),
        unit: new FormControl('minute', Validators.required),
        time_block: new FormControl(1, Validators.compose([Validators.required, Validators.min(1)])),
      }),
      
      special_times: new FormArray([]),
    });
  }
  
  
  
  /** Getter cho special_times */
  get specialTimesArray(): FormArray {
    return this.productServiceForm.get('special_times') as FormArray;
  }

  /** Getter cho merge_block */
  get mergeBlockGroup(): FormGroup {
    return this.productServiceForm.get('merge_block') as FormGroup;
  }
  
  /** Getter cho first_time */
  get firstTimeGroup(): FormGroup {
    return this.productServiceForm.get('first_time') as FormGroup;
  }



  initData() {
    this.list_category = this.vhQueryCafe.getlocalCategorys(2);
    this.vhQueryCafe.getlocalProducts().forEach((item) => {
      if (item.barcode) this.list_barcode.push(item);
    });
  }



  /**
   * to barcode t ng
   * @example this.changeBarcode()
   */
  changeBarcode() {
    let newbarcode = '';
    for (let index = 0; index < 12; index++) {
      newbarcode += Math.floor(Math.random() * 10);
    }
    if (!this.checkBarcode(newbarcode)) {
      this.productServiceForm.controls['barcode'].setValue(newbarcode);
    }
  }

  

  /**
   * kim tra barcode  tn ti cha
   * @example this.checkBarcode()
   */
  checkBarcode(barcode) {
    let detailproduct = this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode);
    if (detailproduct == null) {
      let index = this.list_barcode.findIndex(
        (item) => item.barcode == barcode
      );
      if (index >= 0) this.barcode = true;
      else this.barcode = false;
    } else {
      this.barcode = true;
    }
    return this.barcode;
  }



  getFile() {
    document.getElementById('file-upload').click();
  }



  /** Ly hnh nh t Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage
      .getThumbnailFromDesktop(
        file,
        256,
        256,
        'products',
        this.productServiceForm.value.img || null
      )
      .then(
        (photoURL) => {
          this.productServiceForm.controls['img'].setValue(photoURL);
          this.vhComponent.showToast(
            2000,
            this.languageService.translate('Image was loaded successfully'),
            'success-toast'
          );
        },
        () => {
          this.vhComponent.showToast(
            2000,
            this.languageService.translate('Image was failed'),
            'alert-toast'
          );
        }
      );
  }



  /**To mi dch v */
  saveProduct(formValue) {
    let service: any = {
      name: formValue.name,
      time_warning: formValue.time_warning ? parseFloat(formValue.time_warning) : 0,
      img: formValue.img,
      id_category: formValue.id_category,
      // allow_sell: formValue.allow_sell,
      delete_hidden: formValue.delete_hidden,
      selling: formValue.selling,
      type: formValue.type,
      tax: 0,
      units: [
        {
          unit: formValue.unit,
          ratio: 1,
          default: true,
          price: parseFloat(this.price.getRawValue()),
          price2: this.management_product_price2 ? parseFloat(this.price2.getRawValue()) : 0,
          barcode: formValue.barcode,
        },
      ],
    };

    if (formValue.type == VhType.SERVICETIME) {
      // set li units[0] dng time
      service.units[0].price = parseFloat(this.cleaveJsService.getRawValue('.price'));
      service.units[0].price2 = this.management_product_price2 ? parseFloat(this.cleaveJsService.getRawValue('.price2')) : 0;
      service.units[0].time_block = formValue.time_block;

      // cc trng is_merge_block, is_first_time, is_special_times
      service.units[0].is_merge_block = formValue.is_merge_block;
      service.units[0].is_first_time = formValue.is_first_time;
      service.units[0].is_special_times = formValue.is_special_times;

      // merge_block
      if(service.units[0].is_merge_block) {
        service.units[0].merge_block = {
          price: parseFloat(this.cleaveJsService.getRawValue('.merge_block_price')),
          price2: this.management_product_price2 ? parseFloat(this.cleaveJsService.getRawValue('.merge_block_price2')) : 0,
          number: formValue.merge_block.number,
        };
      }
      // first_time
      if (formValue.is_first_time) {
        service.units[0].first_time = {
          price: parseFloat(this.cleaveJsService.getRawValue('.first_time_price')),
          price2: this.management_product_price2 ? parseFloat(this.cleaveJsService.getRawValue('.first_time_price2')) : 0,
          unit: formValue.first_time.unit,
          time_block: formValue.first_time.time_block,
        };
      }
      // special_times
      if (formValue.is_special_times) {
        service.units[0].special_times = [];
        this.specialTimesArray.controls.forEach((item: any) => {
          let data = {
            price: parseFloat(item.value.price),
            price2: this.management_product_price2 ? parseFloat(item.value.price2) : 0,
            time_slot_name: item.value.time_slot_name,
            specific_time: {
              from_minute: item.value.specific_time.from_minute,
              to_minute: item.value.specific_time.to_minute,
            },
            days_of_week: item.value.days_of_week.map((day) => day),
          };
          service.units[0].special_times.push(data);
        });
      }
    }

    this.vhComponent.showLoading('').then(() => {
      this.vhQueryCafe.addProduct_Cafe(service).then(
        (res) => {
          console.log(res);
          this.createProd = true;
          this.vhComponent.showToast(
            1000,
            service.name +
              ' ' +
              this.languageService.translate('has been added successfully'),
            'success-toast'
          );
          setTimeout(() => {
            this.vhComponent.hideLoading(0);
            this.goBack();
          }, 300);
        },
        (err) => {
          this.vhComponent.hideLoading(0);
          console.log(err);
          this.vhComponent.showToast(
            1500,
            this.languageService.translate('An error occurred while adding product. Please try again'),
            'alert-toast'
          );
        }
      );
    });
  }



  /**
   * Khi thay i loi dch v
   * @param event loi dch v c chn
   */
  onServiceTypeChange(event: VhType) {
    if (event == VhType.SERVICETIME) {
      this.productServiceForm.controls['unit'].setValue('minute');
      // thm validator cho cc trng
      this.addValidatorsToFields(this.productServiceForm, ['time_block'], [Validators.compose([Validators.required, Validators.min(1)])]);
      this.addValidatorsToFields(this.productServiceForm, ['is_merge_block', 'is_first_time', 'is_special_times'], [Validators.required]);
      this.productServiceForm.controls['time_block'].setValue(1);
      this.productServiceForm.controls['is_merge_block'].setValue(false);
      this.productServiceForm.controls['is_first_time'].setValue(false);
      this.productServiceForm.controls['is_special_times'].setValue(false);
      this.productServiceForm.controls['merge_block'].get('price').setValue(0);
      this.productServiceForm.controls['merge_block'].get('price2').setValue(0);
      this.productServiceForm.controls['merge_block'].get('number').setValue(1);
      this.productServiceForm.controls['first_time'].get('price').setValue(0);
      this.productServiceForm.controls['first_time'].get('price2').setValue(0);
      this.productServiceForm.controls['first_time'].get('unit').setValue('minute');
      this.productServiceForm.controls['first_time'].get('time_block').setValue(1);
    }
    else {
      this.productServiceForm.controls['unit'].setValue('');
      // xa validator cho cc trng
      this.removeValidatorsFromFields(this.productServiceForm, ['time_block', 'is_merge_block', 'is_first_time', 'is_special_times']);
      this.removeValidatorsFromFields(this.mergeBlockGroup, ['price', 'price2', 'number']);
      this.removeValidatorsFromFields(this.firstTimeGroup, ['price', 'price2', 'unit', 'time_block']);
      this.specialTimesArray.clear();

      this.vhAlgorithm.waitingStack().then(() => {
        this.price = this.vhAlgorithm.vhnumeral('.price');
        if (this.management_product_price2) {
          this.price2 = this.vhAlgorithm.vhnumeral('.price2');
        }
      });
    }
    this.initPriceCleave(event == VhType.SERVICETIME, ['.price']);
    if(this.management_product_price2) this.initPriceCleave(event == VhType.SERVICETIME, ['.price2']);
  }



  /**
   * Khi thay i C gp block hay khng
   * @param event true l c gp block, false l khng 
   */
  changeMergeBlock(event) {
    if(event) {
      this.addValidatorsToFields(this.mergeBlockGroup, ['price', 'price2'], this.PRICE_VALIDATOR);
      this.addValidatorsToFields(this.mergeBlockGroup, ['number'], [Validators.required]);
    }
    else {
      this.removeValidatorsFromFields(this.mergeBlockGroup, ['price', 'price2', 'number']);
    }
    this.initPriceCleave(event, ['.merge_block_price']);
    if(this.management_product_price2) this.initPriceCleave(event, ['.merge_block_price2']);
  }



  /**
   * Khi thay i C s dng gi u tin hay khng
   * @param event true l c s dng gi u tin, false l khng
   */
  changeFirstTime(event) {
    if(event) {
      this.addValidatorsToFields(this.firstTimeGroup, ['price', 'price2'], this.PRICE_VALIDATOR);
      this.addValidatorsToFields(this.firstTimeGroup, ['unit'], [Validators.required]);
      this.addValidatorsToFields(this.firstTimeGroup, ['time_block'], [Validators.required, Validators.compose([Validators.required, Validators.min(1)])]);
    }
    else {
      this.removeValidatorsFromFields(this.firstTimeGroup, ['price', 'price2', 'unit', 'time_block']);
    }
    this.initPriceCleave(event, ['.first_time_price']);
    if(this.management_product_price2) this.initPriceCleave(event, ['.first_time_price2']);
  }



  /**
   * Khi to / Hu Cleave cho cc trng gi tin
   * @param event true l khi to Cleave, false l hu Cleave
   * @param name tn ca cc trng cn khi to Cleave
   */
  initPriceCleave(event: boolean, names: any[]) {
    if (event) {
      setTimeout(() => {
        this.cleaveJsService.createCleaves(names);
      }, 100);
    } 
    else {
      this.cleaveJsService.destroyCleaves(names);
    }
  }



  /**
   * Nhn d liu t modal chn thi gian c bit
   * @param data object special_time c tr v. Nu data c index th l Edit, ngc li l Add
   */
  onOkSpecialTime(data: any) {
    // Nu c index th l Edit
    if(data.index !== undefined) {
      // ly days_of_week t data v set
      const daysOfWeekArray = this.specialTimesArray.at(data.index).get('days_of_week') as FormArray;
      daysOfWeekArray.clear();
      (data.days_of_week || []).forEach((day: string) => {
        daysOfWeekArray.push(new FormControl(day));
      });
      this.specialTimesArray.at(data.index).patchValue({
        price: data.price,
        price2: this.management_product_price2 ? data.price2 : 0,
        time_slot_name: data.time_slot_name,
        specific_time: {
          from_minute: data.specific_time.from_minute,
          to_minute: data.specific_time.to_minute,
        },
      });
    }
    // Add
    else {
      // ly days_of_week t data
      const selectedDays = data.days_of_week || [];
      const daysOfWeekArray = new FormArray(
        selectedDays.map((day: string) => new FormControl(day))
      );
      this.specialTimesArray.push(
        new FormGroup({
          price: new FormControl(data.price),
          price2: new FormControl(this.management_product_price2 ? data.price2 : 0),
          time_slot_name: new FormControl(data.time_slot_name),
          specific_time: new FormGroup({
            from_minute: new FormControl(data.specific_time.from_minute),
            to_minute: new FormControl(data.specific_time.to_minute),
          }),
          days_of_week: daysOfWeekArray,
        })
      );
    }

  }
  
  
  
  /**
   * Chuyn t pht sang gi
   * @param minutes pht cn chuyn i
   * @returns chui thi gian theo nh dng HH:mm
   * @example
   * convertMinutesToTime(90) => '01:30'
   */
  convertMinutesToTime(minutes: number): string {
    const hours = Math.floor(minutes / 60);
    const mins = minutes % 60;
    return `${hours.toString().padStart(2, '0')}:${mins.toString().padStart(2, '0')}`;
  }



  /**
   * Chuyn i pht thnh i tng Date
   * @param minutes S pht cn chuyn i
   * @returns i tng Date vi gi v pht  c thit lp
   * 
   * @example 
   * convertMinutesToDate(90) => Date object with hours = 1, minutes = 30
   * convertMinutesToDate(120) => Date object with hours = 2, minutes = 0
   */
  convertMinutesToDate(minutes: number): Date {
    const date = new Date();
    date.setHours(Math.floor(minutes / 60), minutes % 60, 0, 0); // Set hours and minutes
    return date;
  }



  /**
   * Xa thi gian c bit
   * @param index index ca thi gian c bit cn xa
   */
  deleteSpecialTime(index: number) {
    this.specialTimesArray.removeAt(index);
  }



  /**
   * Chnh sa thi gian c bit
   * @param index index ca thi gian c bit cn chnh sa
   */
  editSpecialTime(index: number) {
    const specialTime = this.specialTimesArray.at(index).value;
    this.dataSpecialTimeEdit = JSON.parse(JSON.stringify(specialTime));
    this.dataSpecialTimeEdit.specific_time.from_minute = this.convertMinutesToDate(this.dataSpecialTimeEdit.specific_time.from_minute);
    this.dataSpecialTimeEdit.specific_time.to_minute = this.convertMinutesToDate(this.dataSpecialTimeEdit.specific_time.to_minute);
    this.dataSpecialTimeEdit.index = index;
    
    this.isShowModalSpecialTime = true;
  }



  /**
   * Thm validator vo cc trng trong FormGroup
   * 
   * @param group FormGroup cn thm validator.
   * @param fields Mng tn trng m validator s c thm vo.
   * @param validators Mng cc validator cn thm.
   * 
   * @example
   * addValidatorsToFields(this.productServiceForm, ['field1', 'field2'], [Validators.required]);
   * addValidatorsToFields(this.productServiceForm, ['field1', 'field2'], [Validators.required, Validators.minLength(3)]);
   */
  addValidatorsToFields(group: FormGroup, fields: string[], validators: any[]) {
    fields.forEach((field) => {
      const control = group.get(field);
      if (control) {
        control.addValidators(validators);
        control.updateValueAndValidity();
      }
    });
  }



  /**
   * Xa validator khi cc trng trong FormGroup
   * Nu khng ch nh validator, tt c cc validator s b xa.
   * 
   * @param group FormGroup cn xa validator.
   * @param fields Mng tn trng m validator s b xa.
   * @param validators (Optional) Cc validator cn xa. Nu khng cung cp, tt c cc validator s b xa.
   * 
   * @example
   * removeValidatorsFromFields(this.productServiceForm, ['field1', 'field2']);
   * removeValidatorsFromFields(this.productServiceForm, ['field1', 'field2'], [Validators.required]);
   * removeValidatorsFromFields(this.productServiceForm, ['field1', 'field2'], [Validators.required, Validators.minLength(3)]);
   */
  removeValidatorsFromFields(group: FormGroup, fields: string[], validators?: any[]) {
    fields.forEach((field) => {
      const control = group.get(field);
      if (control) {
        // Xa cc validator  ch nh
        if (validators && validators.length > 0) {
          validators.forEach((validator) => {
            control.removeValidators(validator);
          });
        } 
        // Xa tt c cc validator
        else {
          control.clearValidators();
        }
        control.updateValueAndValidity();
      }
    });
  }
      
      
      
  /** Nu l true th l ang thm danh mc, dng  hin th trng thi loading, trnh b nhn nhiu ln */
  isAddingCategory: boolean = false;
  /** Input nhp tn danh mc */
  @ViewChild('categoryName') categoryName;

  /**
   * Thm danh mc
   */
  addCategory(categoryName: any) {
    const value = categoryName.value ? categoryName.value?.toString()?.trim() : '';
    if (value) {
      // Kim tra xem danh mc  tn ti hay cha
      let index = this.vhQueryCafe.getlocalCategorys()
                    .filter(res => res.type == VhType.SERVICE)
                    .findIndex(item => this.vhAlgorithm.changeAlias(item.name).toLowerCase() == this.vhAlgorithm.changeAlias(value).toLowerCase());

      //  tn ti
      if (index !== -1) {
        this.vhComponent.showToast(1500, this.languageService.translate("Category existed"), 'alert-toast');
        return;
      }

      // Cha tn ti th thm
      this.isAddingCategory = true;
      this.vhQueryCafe.addCategory({ name: value, type: VhType.SERVICE }).then((res: any) => {
        this.categoryName.nativeElement.value = ''; // Reset input
        this.list_category.unshift(res); // Thm vo u danh sch
        this.productServiceForm.controls['id_category'].setValue(res._id); // Chn ci mi thm
        this.vhComponent.showToast(2000, (this.languageService.translate("Category has been added successfully")), 'success-toast');
      }, err => {
        console.log(err);
        this.vhComponent.showToast(1500, this.languageService.translate("An eror occured while adding category. Please try again"), 'alert-toast');
      }).finally(() => {
        this.isAddingCategory = false;
      });
    }
    else {
      this.vhComponent.showToast(1500, this.languageService.translate('Category name is required'), 'alert-toast');
    }
  }

}
