import { Injectable } from '@angular/core';
import Cleave from 'cleave.js';

/**
 * Qun l cc cleavejs instances  trnh memory leak
 */
@Injectable({
  providedIn: 'root'
})
export class CleaveJsService {

  private cleaveInstances: Map<string, Cleave> = new Map();



  /**
   * To cleave instance vi selector v options c ch nh
   * @param selector dng  xc nh element cn to cleave instance, s c truyn vo `document.querySelector` 
   * @param options cc options ca `cleave.js`, mc nh nu khng truyn vo l `{ numeral: true, numeralThousandsGroupStyle: 'thousand' }`
   */
  createCleave(selector: string, options?: any): void {
    // Kim tra xem c trn DOM khng
    if(!document.querySelector(selector)) {
      return;
    }
    // Nu  tn ti th destroy
    if (this.cleaveInstances.has(selector)) {
      this.destroyCleave(selector);
    }
    // To mi
    const defaultOptions = {
      numeral: true,
      numeralThousandsGroupStyle: 'thousand'
    };
    const finalOptions = { ...defaultOptions, ...options };
    this.cleaveInstances.set(selector, new Cleave(document.querySelector(selector), finalOptions));
  }



  /**
   * To cleave instance vi selectors v options c ch nh
   * @param selectors dng  xc nh element cn to cleave instance, s c truyn vo `document.querySelector`. VD: ['.input1', '.input2']
   * @param options cc options ca `cleave.js`, mc nh nu khng truyn vo l `{ numeral: true, numeralThousandsGroupStyle: 'thousand' }`
   */
  createCleaves(selectors: any[], options?: any): void {
    selectors?.forEach((selector) => {
      // Kim tra xem c trn DOM khng
      if(!document.querySelector(selector)) {
        // console.log(`CleaveJsService: Not found element with selector ${selector}`);
        return;
      }
      // Nu  tn ti th destroy
      if (this.cleaveInstances.has(selector)) {
        this.destroyCleave(selector);
      }
      // To mi
      const defaultOptions = {
        numeral: true,
        numeralThousandsGroupStyle: 'thousand'
      };
      const finalOptions = { ...defaultOptions, ...options };
      this.cleaveInstances.set(selector, new Cleave(document.querySelector(selector), finalOptions));
    });
  }



  /**
   * Ly gi tr khi cha qua x l. Nu cha c th s t to mi
   * @param selector dng  xc nh element cn ly gi tr, s c truyn vo `document.querySelector`
   * @returns gi tr khi cha qua x l
   */
  getRawValue(selector: string): any {
    if (!this.cleaveInstances.has(selector)) {
      this.createCleave(selector);
    }
    return this.cleaveInstances.get(selector).getRawValue();
  }



  /**
   * Destroy cleave vi selector c ch nh
   * @param selector dng  xc nh element cn destroy, s c truyn vo `document.querySelector` 
   */
  destroyCleave(selector: string) {
    if (this.cleaveInstances.has(selector)) {
      this.cleaveInstances.get(selector).destroy();
      this.cleaveInstances.delete(selector);
    }
  }



  /**
   * Destroy danh sch cleave vi mng selector c ch nh
   * @param selector dng  xc nh cc element cn destroy, s c truyn vo `document.querySelector` 
   */
  destroyCleaves(selectors: string[]) {
    selectors?.forEach((selector) => {
      if (this.cleaveInstances.has(selector)) {
        this.cleaveInstances.get(selector).destroy();
        this.cleaveInstances.delete(selector);
      }
    });
  }



  /**
   * Destroy tt c cleave instances
   */
  destroyAllCleave() {
    this.cleaveInstances?.forEach((cleave) => {
      cleave.destroy();
    });
    this.cleaveInstances.clear();
  }

}
