import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ServicesListComponent} from './services-list.component';
import { NgZorroAntModule} from '../../../../ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AddServiceComponent } from './add-service/add-service.component';
import { DetailServiceComponent } from './detail-service/detail-service.component';
import { TranslateModule } from '@ngx-translate/core';
import { CreateDataServiceComponent } from './create-data-service/create-data-service.component';
import { SpecialTimeComponent } from './special-time/special-time.component';
const routes: Routes = [
  {
    path: '',
    component: ServicesListComponent
  },
  {
    path: 'add',
    component: AddServiceComponent
  },
  {
    path: 'detail',
    component: DetailServiceComponent
  },
  {
    path: 'create-data-service',
    component: CreateDataServiceComponent
  }
]

@NgModule({
  declarations: [
    ServicesListComponent,
    AddServiceComponent,
    DetailServiceComponent,
    CreateDataServiceComponent,
    SpecialTimeComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'}),
    TranslateModule
  ]
})
export class ServicesListModule { }
